<template>
  <div>
    <el-alert title="" type="info" center show-icon :closable='false'>
    </el-alert>
    <!--  -->
    <el-steps :active="stepActive" align-center>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>
    <!-- ,tabsform -->
    <el-form :model="addCateForm" :rules="addCateFormRules" ref="addCateFormRef" label-position="top" label-width="100px">
      <!--  -->
      <el-tabs tab-position="left" @tab-click="tabClick" :before-leave="beforeTab">
        <el-tab-pane label="">
          <el-form-item label="" prop="goods_name">
            <el-input v-model="addCateForm.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_price">
            <el-input v-model="addCateForm.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_weight">
            <el-input v-model="addCateForm.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_number">
            <el-input v-model="addCateForm.goods_number"></el-input>
          </el-form-item>
          <el-form-item label="" prop="goods_cat">
            <!-- number -->
            <el-cascader v-model="addCateForm.goods_cat" ref="cascaderRef" :options="catesList" placeholder="" @change="handleChange" :props="props"
              clearable filterable>
            </el-cascader>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
          <el-form-item :label="item.attr_name" v-for="(item) in paramsList" :key="item.attr_id">
            <el-checkbox-group v-model="item.attr_value" @change="cheChange">
              <el-checkbox :label="cb" border v-for="(cb,i) in item.attr_vals" :key="i">
              </el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
          <el-form-item :label="item.attr_name" v-for="(item) in attrsList" :key="item.attr_id">
            <el-input v-model="item.attr_value" placeholder=""></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
          <!--  -->
          <el-upload :action="uploadURL" :on-preview="handlePreview" :on-remove="handleRemove" :on-success="handleSuccess" :file-list="addCateForm.pics"
            :headers="tokenHeaders" list-type="picture">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
          </el-upload>
        </el-tab-pane>
        <el-tab-pane label="">
          <quill-editor ref="myTextEditor" v-model="addCateForm.goods_introduce" :options="editorOption"></quill-editor>
          <el-button type="primary" icon="el-icon-goods" @click="addCate"></el-button>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <!--  -->
    <el-dialog title="" :visible.sync="picDialogVisible" width="50%">
      <img :src="picPreview" alt="" width="100%">
    </el-dialog>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      // 
      stepActive: 0,
      // 
      addCateForm: {
        goods_name: '',
        goods_cat: [],
        goods_price: 1,
        goods_number: 1,
        goods_weight: 1,
        goods_introduce: '',
        pics: [],
        attrs: []
      },
      // 
      paramsList: [],
      // 
      attrsList: [],
      props: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        expandTrigger: 'hover'
      },
      // url
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // token
      tokenHeaders: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      editorOption: {
        placeholder: ''
      },
      // 
      fileList: [],
      // 
      catesList: [],
      // Preview
      picPreview: '',
      // 
      picDialogVisible: false,
      // 
      addCateFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 25, message: ' 3  8 ', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getCatesList()
    this.getCateForm()
    console.log(this.$route.params.id)
  },
  methods: {
    // tab
    tabClick (tab) {
      // console.log(tab)
    },
    cheChange (value) {
      console.log(this.paramsList)
    },
    // 
    async getCatesList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.catesList = res.data
    },
    // 
    async getCateForm () {
      const { data: res } = await this.$http.get(`goods/${this.$route.params.id}`)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      res.data.pics.forEach(item => {
        item.url = item.pics_big_url
        item.name = item.pics_id
      })
      // 
      res.data.attrs.forEach(item => {
        if (item.attr_sel === 'only') {
          this.attrsList.push({
            attr_name: item.attr_name,
            attr_vals: item.attr_vals,
            attr_value: item.attr_value,
            attr_id: item.attr_id
          })
        } else {
          this.paramsList.push({
            attr_name: item.attr_name,
            // valsvalue
            attr_vals: item.attr_vals ? item.attr_vals.split(' ') : [],
            attr_value: item.attr_value ? item.attr_value.split(' ') : [],
            attr_id: item.attr_id
          })
        }
      })
      // goods_cat,.map
      res.data.goods_cat = res.data.goods_cat.split(',')
      res.data.goods_cat = res.data.goods_cat.map(Number)
      this.addCateForm = res.data
      console.log(this.addCateForm)
    },
    handleChange (value) {
      console.log(value)
      console.log(this.addCateForm)
      if (this.addCateForm.goods_cat.length !== 3) {
        this.addCateForm.goods_cat = []
        return this.$message.error('')
      } else {
        // this.getAttrList()
      }
    },
    beforeTab (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addCateForm.goods_cat.length !== 3) {
        this.$message.error('')
        this.stepActive = 0
        return false
      }
      // stepActivenumber0number
      this.stepActive = (activeName - 0) + 1
    },
    // ,
    handlePreview (file) {
      console.log(file)
      // 
      if (file.response) {
        this.picPreview = file.response.data.url
        return
      } else {
        this.picPreview = file.url
      }

      this.picDialogVisible = true
    },
    // 
    handleRemove (file) {
      // 
      console.log(file)
      if (file.response) {
        this.addCateForm.pics.splice(this.addCateForm.pics.indexOf({ pic: file.response.data.tmp_path }), 1)
      } else {
        this.addCateForm.pics.splice(this.addCateForm.pics.indexOf(file), 1)
      }
    },
    // ,tmp_path
    handleSuccess (res) {
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.addCateForm.pics.push({
        pic: res.data.tmp_path,
        url: res.data.url,
        name: res.data.tmp_path

      })
      console.log(this.addCateForm)
    },
    // 
    addCate () {
      this.$refs.addCateFormRef.validate(async callback => {
        if (!callback) return this.$message.error('')
        // 
        const form = _.cloneDeep(this.addCateForm)
        // 
        form.attrs = []
        this.paramsList.forEach(item => {
          form.attrs.push({
            attr_id: item.attr_id,
            //  valuevals
            attr_value: item.attr_value.join(' '),
            attr_vals: item.attr_vals.join(' ')
          })
        })
        this.attrsList.forEach(item => {
          form.attrs.push({
            attr_id: item.attr_id,
            attr_value: item.attr_value,
            attr_vals: item.attr_vals
          })
        })
        form.goods_cat = form.goods_cat.join(',')
        console.log(form)
        const { data: res } = await this.$http.put(`goods/${this.$route.params.id}`, form)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.$router.push('/goods')
      })
    }

  }
}
</script>
<style lang="less" scoped>
.el-button {
  margin-top: 20px;
}
</style>
