<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/goods' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <!--  -->
          <!-- x -->
          <el-input v-model="pageList.query" placeholder="" prefix-icon="el-icon-search" clearable @clear="getCateList">
            <!-- query -->
            <el-button slot="append" icon="el-icon-search" @click="getCateList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="8">
          <el-button type="primary" icon="el-icon-goods" @click="addCate"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="cateList" stripe style="width: 100%" border>
        <el-table-column type="index" label="" width="50">
        </el-table-column>
        <el-table-column prop="goods_name" label="" min-width="500">
        </el-table-column>
        <el-table-column prop="goods_price" label="" width="120">
        </el-table-column>
        <el-table-column prop="goods_number" label="">
        </el-table-column>
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            {{scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" width="180">
          <template slot-scope="scope">
            <!--  -->
            <el-button type="primary" icon="el-icon-edit" @click="editorDialog(scope.row.goods_id)" size="mini"></el-button>
            <!--  -->
            <el-button type="danger" icon="el-icon-delete" @click="delCate(scope.row.goods_id)" size="mini"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- page -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageList.pagenum" :page-sizes="[50, 30, 10, 5]"
        :page-size="pageList.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      pageList: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      cateList: []
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    async getCateList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.pageList
      })
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.total = res.data.total
      this.cateList = res.data.goods
    },
    delCate (id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.delete(`goods/${id}`)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        // elementbug
        const totalPage = Math.ceil((this.total - 1) / this.pageList.pagesize) // 
        this.pageList.pagenum = this.pageList.pagenum > totalPage ? totalPage : this.pageList.pagenum
        this.pageList.pagenum = this.pageList.pagenum < 1 ? 1 : this.pageList.pagenum
        this.getCateList()
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    editorDialog (id) {
      // 
      this.$router.push({ name: 'editor', params: { id } })
    },
    // 
    handleSizeChange (size) {
      this.pageList.pagesize = size
      this.getCateList()
    },
    // 
    handleCurrentChange (current) {
      this.pageList.pagenum = current
      this.getCateList()
    },
    addCate () {
      this.$router.push('/goods/addcate')
    }
  }
}
</script>
